/*  lyt_table_order(2017-07-07)
==================================================*/
.lyt_order table {
  margin-bottom: 60px;
}
.lyt_order .odr_detail {
  text-align: left !important;
  color: #333;
  font-size: 14px;
}

.lyt_order .odr_detail img {
  width: 100px;
  height: 100px;
  float: left;
  margin: 0 20px;
  border: 1px solid #e5e5e5;
}

.lyt_order .odr_detail span {
  display: table;
  height: 100px;
}

.lyt_order .odr_detail span i {
  display: table-cell;
  vertical-align: middle;
}

.lyt_order .odr_total {
  text-align: right;
  padding-right: 20px;
  background-color: #f6f5fa;
}

.lyt_order .odr_total .clr_rd {
  font-weight: bold;
}
.lyt_order .odr_dtl_bom p{
  color: #999;
}
.lyt_order .submit_order{
  display: block;
  padding: 8px 40px;
  float: right;
  color: #fff;
  margin-top: 10px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background-color: #6f0afe;
}




