.lyt_uc_form {
  border: 1px solid #e5e5e5;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background-color: #fff;
  overflow: hidden;
}

.lyt_uc_form .ucf_title {
  padding: 15px 20px;
  border-bottom: 1px solid #e5e5e5;
}

.lyt_uc_form .ucf_main {
  padding: 20px;
}

.lyt_uc_form .ucf_tips {
  color: #8b6d39;
  padding: 15px;
  border: 1px solid #faebcc;
  -webkit-border-radius: 5px;
  -moz-border-radius: 5px;
  border-radius: 5px;
  background-color: #fdf8e4;
}

.lyt_uc_form .ucf_tips strong {
  font-weight: bold;
}

.lyt_uc_form .ucf_form {
  margin-top: 20px;
}

.lyt_uc_form .fm_item:after{
  display: block;
  content: "";
  clear: both;
  height: 0;
  visibility: hidden;
}
.lyt_uc_form .fm_item {
  min-height: 42px;
  margin-bottom: 20px;
}

.lyt_uc_form .fm_item label,
.lyt_uc_form .fm_item input,
.lyt_uc_form .fm_item textarea,
.lyt_uc_form .fm_item span {
  display: block;
  float: left;
}

.lyt_uc_form .fm_item label {
  width: 130px;
  line-height: 42px;
  text-align: right;
  margin-right: 10px;
}

.lyt_uc_form .fm_item input {
  width: 400px;
  height: 40px;
  padding-left: 10px;
  border: 1px solid #bbbbbb;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
.lyt_uc_form .fm_item textarea {
  width: 550px;
  height: 120px;
  resize: none;
  overflow-y: hidden;
  border: 1px solid #bbbbbb;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
.lyt_uc_form .fm_item span {
  margin: 12px 0 0 15px;
}

.lyt_uc_form .fm_btns {
  height: 40px;
  padding-left: 140px;
}

.lyt_uc_form .fm_btns button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  display: block;
  width: 120px;
  height: 40px;
  line-height: 40px;
  text-align: center;
  float: left;
  border: 0;
  outline: none;
  cursor: pointer;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}

.lyt_uc_form .btn_save {
  color: #fff;
  margin-right: 20px;
  background-color: #6f0afe;
}

.lyt_uc_form .btn_cancel {
  border: 1px solid #dddddd !important;
  background-color: #ffffff;
}



