.lyt_address {
  height: 160px;
}

.lyt_address .addr_wrap {
  width: 109%;
}

.lyt_address .addr_item {
  position: relative;
  width: 29%;
  height: 155px;
  float: left;
  padding: 15px;
  margin: 0 25px 25px 0;
  cursor: pointer;
  -webkit-box-sizing: border-box;
  -moz-box-sizing: border-box;
  box-sizing: border-box;
  border: 2px solid #e5e5e5;
  background-color: #fff;
}
.lyt_address .default{
  position: relative;
  border-color: #700aff;
}
.lyt_address .default:after{
  position: absolute;
  right: 0;
  bottom: 0;
  display: block;
  width: 44px;
  height: 30px;
  content: "";
  background: url("../../assets/icon_layout.png");
  background-position:6px -119px;
}
.lyt_address .addr_item h3 {
  padding-bottom: 5px;
  font-weight: normal;
  border-bottom: 1px solid #e5e5e5;
}

.lyt_address .addr_item h3 span {
  font-size: 14px;
}

.lyt_address p span {
  font-size: 14px;
  cursor: pointer;
}

.lyt_address .addr_deil {
  max-height: 66px;
  line-height: 22px;
  margin-top: 10px;
  overflow: hidden;
}

.lyt_address .addr_btns {
  position: absolute;
  left: 15px;
  bottom: 10px;
}

.lyt_address .addr_edit {
  color: #ff0000;
  margin-right: 20px;
}

.lyt_address .addr_insert {
  font-weight: bold;
  cursor: pointer;
  border: 2px dashed #e5e5e5;
  background-color: #f8f9fd;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.lyt_address .addr_insert p {
  margin-top: 50px;
}

.lyt_address .addr_insert p i {
  display: block;
  width: 35px;
  height: 35px;
  float: left;
  margin: -7px 10px 0 85px;
  background: url("../../assets/icon_layout.png") no-repeat;
  background-position: -63px -115px;
}



