
@charset "UTF-8";
@import url(../../styles/common/lyt_uc_main.css);
.lyt_pdt_dtl {
  padding: 30px;
  box-sizing: border-box;
  background-color: #fff;
}
.lyt_pdt_dtl .pd_top {
  width: 100%;
  height: 430px;
}
.lyt_pdt_dtl .pd_top_lt {
  display: table;
  width: 450px;
  height: 400px;
  float: left;
  /*    margin-right: 30px;*/
  background-color: #fff;
  border: 1px solid #e5e5e5;
}
.lyt_pdt_dtl .pd_picture {
  display: table-cell;
  vertical-align: middle;
}
.lyt_pdt_dtl .pd_picture img {
  display: block;
  max-height: 400px;
  margin: 0 auto;
}
.lyt_pdt_dtl .pd_top_rt {
  width: 50%;
  float: left;
  padding: 10px 30px;
  box-sizing: border-box;
}
.lyt_pdt_dtl .pd_top_tit {
  max-height: 60px;
  line-height: 20px;
  margin-bottom: 20px;
  overflow: hidden;
}
.lyt_pdt_dtl .pd_top_rt .item {
  width: 100%;
  height: 35px;
  line-height: 35px;
}
.lyt_pdt_dtl .item label {
  display: block;
  width: 85px;
  float: left;
  font-size: 14px;
  padding-left: 10px;
}
.lyt_pdt_dtl .item span {
  display: block;
  float: left;
}
.lyt_pdt_dtl .pd_price {
  background-color: #f7f7f7;
}
.lyt_pdt_dtl .pd_price .clr_rd {
  font-weight: 600;
}
.lyt_pdt_dtl .pd_credit {
  margin: 8px 0;
}
.lyt_pdt_dtl .pd_color,
.lyt_pdt_dtl .pd_size {
  margin-bottom: 15px;
}
.lyt_pdt_dtl .im_box i {
  display: block;
  line-height: normal;
  padding: 7px 15px;
  float: left;
  font-size: 14px;
  margin-right: 15px;
  cursor: pointer;
  border: 1px solid #e5e5e5;
}
.lyt_pdt_dtl .im_box i.active {
  border-color: #000;
}
.lyt_pdt_dtl .im_choice {
  width: 120px;
  height: 33px;
  border: 1px solid #e5e5e5;
}
.lyt_pdt_dtl .im_choice i,
.lyt_pdt_dtl .im_choice input {
  display: block;
  float: left;
  height: 33px;
  line-height: 33px;
  text-align: center;
}
.lyt_pdt_dtl .im_choice i {
  width: 35px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  background-color: #f6f5fa;
}
.lyt_pdt_dtl .im_choice i.disable {
  color: #e0e0e0;
}
.lyt_pdt_dtl .im_choice input {
  width: 50px;
  border: none;
  border-right: 1px solid #e5e5e5;
  border-left: 1px solid #e5e5e5;
  box-sizing: border-box;
}
.lyt_pdt_dtl .pd_btn {
  display: block;
  width: 150px;
  height: 50px;
  line-height: 50px;
  text-align: center;
  margin: 15px 0 0 10px;
  color: #fff;
  border-radius: 5px;
  background-color: #ff3300;
}

/* 详情下面 */
.lyt_pdt_dtl .pd_bom {
  border: 1px solid #e5e5e5;
}
.lyt_pdt_dtl .pd_bom_tit {
  height: 50px;
  line-height: 50px;
  text-indent: 20px;
  background-color: #f6f8f7;
}
.lyt_pdt_dtl .pd_bm_text {
  padding: 20px 15px;
  overflow: hidden;
}
.lyt_pdt_dtl .pd_bm_text ul {
  padding-bottom: 20px;
  border-bottom: 1px solid #e5e5e5;
}
.lyt_pdt_dtl .pd_bm_text ul li {
  width: 25%;
  float: left;
  font-size: 14px;
  padding: 5px 0;
}
.lyt_pdt_dtl .pd_bom_img img {
  display: block;
  margin: 10px auto;
}



