/* lyt_form */
.lyt_form .fm_item {
  position: relative;
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #e5e5e5;
}

.lyt_form .fm_errs {
  position: absolute;
  top: 0;
  left: 0;
  height: 20px;
  line-height: 20px;
}

.lyt_form .fm_item input {
  width: 100%;
  border: none;
  color: #555;
  font-size: 16px;
}
.lyt_form .fm_item select {
  appearance:none;
  -moz-appearance:none;
  -webkit-appearance:none;
  width: 33%;
  float: left;
  padding: 5px 20px;
  margin-top: 15px;
  border: none;
  outline: none;
  color: #dddddd;
  font-size: 16px;
}
.lyt_form .fm_item.half input {
  width: 50%;
  float: left;
  margin-top: 20px;
}

.lyt_form .fm_verifycode {
  width: 50%;
  height: 60px;
  float: right;
  cursor: pointer;
}

.lyt_form .fm_verifycode img {
  float: right;
  margin-top: 8px;
}

.lyt_form .fm_btns {
  margin: 30px 0 20px;
}

.lyt_form .fm_btns button {
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border: none;
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  outline: none;
  color: #fff;
  font-size: 16px;
  letter-spacing: 5px;
  cursor: pointer;
  background-color: #6f53ff;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}



