@import "./ui_form_lgrt.css";
.lyt_login_regist{
  position: relative;
}
.lyt_login_regist .lr_main{
  overflow: hidden;
}
.lyt_login_regist .lr_cont {
  width: 420px;
  min-height: 530px;
  margin: 10% auto;
  padding: 0 70px;
  border-radius: 10px;
  background-color: #fff;
  overflow: hidden;
}

.lyt_login_regist .lr_bg {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  -webkit-transform: translateZ(0);
  background: url("../../assets/login_bg.jpg");
  background-size: cover;
}

.lyt_login_regist .lr_main {
  position: relative;
  z-index: 10;
}

.lyt_login_regist .lr_logo img {
  margin: 48px auto;
}

.lyt_login_regist .fm_bom_link .first{
  color: #6f53ff;
}

.lyt_login_regist .fm_bom_link .last{
  float: right;
  color: #888888;
}



