
.c_areas {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 10000;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5)
}
.c_areas .ar_main {
  position: absolute;
  top: 50%;
  left: 50%;
  z-index: 100;
  width: 480px;
  height: 360px;
  margin: -180px 0 0 -240px;
  padding: 20px;
  background-color: #fff;
  border-radius: 5px;
}
.c_areas .ar_main .ar_top {
  padding-bottom: 10px;
}
.c_areas .ar_main .ar_bom {
  height: 280px;
  border-top: 1px solid #e0e0e0;
  border-bottom: 1px solid #e0e0e0;
}
.c_areas .ar_bom .item {
  width: 160px;
  height: 280px;
  float: left;
  overflow: hidden;
}
.c_areas .ar_bom .item_sub {
  width: 108%;
  height: 100%;
  overflow-y: auto;
  background-color: #f5f5f5;
}
.c_areas .ar_bom .item_sub span {
  display: block;
  padding: 5px 0;
  text-indent: 10px;
  font-size: 14px;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
  cursor: pointer;
}
.c_areas .ar_btn {
  text-align: center;
  padding-top: 20px;
}
.c_areas .ar_btn span {
  display: inline-block;
  padding: 5px 30px;
  margin: 0 10px;
  cursor: pointer;
}
.c_areas .ar_btn span:first-child {
  color: #fff;
  background-color: #6f53ff;
}
.c_areas .ar_btn span:last-child {
  background-color: #eaeaea
}



