
@import url(../../styles/common/lyt_uc_main.css);
.lyt_products {
  width: 100%;
  overflow: hidden;
}
.lyt_products ul {
  width: 110%;
}
.lyt_products ul li {
  position: relative;
  width: 22%;
  height: 315px;
  float: left;
  padding: 15px 10px;
  margin: 0 10px 15px 0;
  box-sizing: border-box;
  border: 1px solid #e5e5e5;
  background-color: #fff;
}
.lyt_products .pdt_top {
  height: 205px;
  padding: 10px 0;
  border-bottom: 1px dashed #a9a9a9;
}
.lyt_products .pdt_top img {
  margin: 0 auto;
  height: 150px;
}
.lyt_products .pdt_top h3 {
  max-height: 44px;
  margin-top: 10px;
  font-weight: normal;
  overflow: hidden;
}
.lyt_products .pdt_top h3 a {
  line-height: 20px;
  font-size: 14px;
}
.lyt_products .pdt_bom {
  position: absolute;
  left: 10px;
  bottom: 10px;
  display: block;
  width: 100%;
  height: 40px;
}
.lyt_products .pdt_bom span {
  display: block;
  float: left;
  color: #ff0000;
  font-size: 24px;
}
.lyt_products .pdt_bom a {
  display: block;
  width: 100px;
  height: 30px;
  line-height: 30px;
  text-align: center;
  float: right;
  color: #fff;
  margin-right: 20px;
  border-radius: 3px;
  background-color: #6f0afe;
}



