
@import url(../../styles/common/lyt_uc_main.css);
@import url(../../styles/common/lyt_table.css);
@import url(../../styles/common/lyt_table_order.css);
.lyt_cart .chkbox {
  float: left;
  margin: 0 15px;
}
.lyt_cart .chk_sub {
  margin-top: 43px;
}
.lyt_cart .cart_bom {
  background-color: #f6f5fa;
}
.lyt_cart .cart_bom_lt {
  text-align: left;
}
.lyt_cart .cart_bom_lt a {
  display: block;
  padding: 8px 25px;
  float: left;
  margin-left: 15px;
}
.lyt_cart .set_number {
  width: 99px;
  border: 1px solid #e5e5e5;
  box-sizing: border-box;
}
.lyt_cart .set_number {
  height: 25px;
  line-height: 25px;
  overflow: hidden;
}
.lyt_cart .set_number span,
.lyt_cart .set_number input {
  display: block;
  float: left;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.lyt_cart .set_number span {
  width: 25px;
  height: 25px;
  cursor: pointer;
}
.lyt_cart .set_number input {
  width: 40px;
  height: 25px;
  line-height: 25px;
  text-align: center;
  border: none;
}
.lyt_cart .set_number .add {
  border-left: 1px solid #e5e5e5;
}
.lyt_cart .set_number .del {
  border-right: 1px solid #e5e5e5;
}
.lyt_cart .del.disable {
  color: #e5e5e5;
}
.lyt_cart .go_on {
  color: #333;
  border: 1px solid #e5e5e5;
  border-radius: 3px;
  background-color: #fff;
}
.lyt_cart .ey_cr {
  color: #999;
}
.lyt_cart .cart_bom_rt {
  text-align: right;
  padding-right: 30px;
}
.lyt_cart .cart_bom_rt p span {
  margin-left: 20px;
  color: #999;
}
.lyt_cart .go_stlt {
  display: block;
  padding: 8px 40px;
  color: #fff;
  float: right;
  margin-top: 20px;
  border-radius: 3px;
  background-color: #6f0afe;
}



