
@import url(../../styles/common/lyt_uc_main.css);
@import url(../../styles/common/lyt_uc_form.css);
.lyt_feedback {
  padding-bottom: 100px;
}
.lyt_feedback .fm_item {
  position: relative;
}
.lyt_feedback .fm_item .help {
  position: absolute;
  left: 125px;
  bottom: -18px;
}
.lyt_feedback .tips {
  display: block;
  float: left;
  color: #999;
  font-size: 14px;
  margin: 10px 0 0 10px;
}
.lyt_feedback .upld_tips {
  color: #999;
  font-size: 14px;
}
.lyt_feedback .fm_btns {
  margin-top: 40px;
}



