
.c_user_info {
  width: 100%;
  height: 110px;
}
.c_user_info .cui_wrap {
  width: 104%;
}
.c_user_info .cui_item {
  width: 17.5%;
  float: left;
  margin-right: 22px;
  padding: 25px 15px;
  color: #fff;
  border-radius: 5px;
  box-sizing: border-box;
}
.c_user_info .set_width .cui_item {
  width: 22.5%;
}
.c_user_info .ci_cont {
  position: relative;
  border-bottom: 2px solid #fff;
}
.c_user_info .cui_wrap .ci1 {
  background-color: #6f0afe;
}
.c_user_info .cui_wrap .ci2 {
  background-color: #f9995f;
}
.c_user_info .cui_wrap .ci3 {
  background-color: #0a7fff;
}
.c_user_info .cui_wrap .ci4 {
  background-color: #f56854;
}
.c_user_info .cui_wrap .ci5 {
  background-color: #17c877;
}
.c_user_info .ci_cont p {
  padding: 8px 0;
  font-size: 12px;
}
.c_user_info .ci_cont i {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 40px;
  height: 40px;
  background: url("../assets/icon_layout.png") no-repeat;
}
.c_user_info .ci_cont i.ci1_icon {
  background-position: 8px -70px;
}
.c_user_info .ci_cont i.ci2_icon {
  background-position: -40px -70px;
}
.c_user_info .ci_cont i.ci3_icon {
  background-position: -132px -70px;
}
.c_user_info .ci_cont i.ci4_icon {
  background-position: -87px -70px;
}
.c_user_info .ci_cont i.ci5_icon {
  background-position: -189px -70px;
}
.c_user_info .ci_cont i.ci6_icon {
  background-position: -238px -70px;
}



