/*  reset(2017-07-04)
==================================================*/
@charset "utf-8";body{color:#333;font-size:16px;font-family:"Microsoft YaHei";overflow-x: hidden;}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,form,fieldset,input,textarea,p,blockquote,th,td{margin:0;padding:0}
table{border-collapse:collapse;border-spacing:0}
fieldset,img{border:0 none;display:block}
address,caption,cite,code,dfn,em,i,u,b,strong,th,var{font-style:normal;font-weight:normal}
input,textarea{outline:0 none;color:#333;font-size:14px;font-family:"Microsoft YaHei"}
input:-webkit-autofill { -webkit-box-shadow: 0 0 0px 1000px white inset !important;  }
::-moz-placeholder { color: #bbbbbb; }
::-webkit-input-placeholder { color:#bbbbbb; }
:-ms-input-placeholder { color:#bbbbbb; }
select::-ms-expand { display: none; }
ol,ul{list-style:none}
caption,th{text-align:left}
h1,h2,h3,h4,h5,h6{font-size:100%;}
a{color:#333;text-decoration:none}
img{border:0 none;max-width:100%}
i,em{font-style:normal}
.clearfix:after{display: block;content: "";clear: left;height: 0;overflow: hidden;visibility: hidden;}
.clearfix{*zoom:1}
/* common */
.help.is-danger{ color: #ff0000; font-size: 14px;}
.clr_rd{color: #ff0000 !important;}
.clr_gy{color: #999 !important;}



