
@import url(../../styles/common/lyt_uc_main.css);
@import url(../../styles/common/lyt_uc_form.css);
.lyt_deposit .dpt_sum {
  overflow: hidden;
}
.lyt_deposit dl dt {
  padding: 20px 0 10px;
}
.lyt_deposit .dpt_sum dd {
  width: 560px;
}
.lyt_deposit .dpt_sum span {
  display: block;
  width: 118px;
  height: 48px;
  line-height: 48px;
  text-align: center;
  float: left;
  cursor: pointer;
  margin: 0 20px 20px 0;
  border: 1px solid #dddddd;
  border-radius: 3px;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.lyt_deposit .default {
  position: relative;
  border-color: #700aff !important;
}
.lyt_deposit .default:after {
  position: absolute;
  top: 0;
  right: 0;
  display: block;
  width: 30px;
  height: 30px;
  content: "";
  background: url("../../assets/icon_layout.png");
  background-position: -104px -116px;
}
.lyt_deposit .pay_way {
  height: 45px;
}
.lyt_deposit .pay_way span {
  display: block;
  width: 158px;
  height: 43px;
  line-height: 43px;
  text-align: center;
  float: left;
  margin-right: 20px;
  cursor: pointer;
  border: 1px solid #dddddd;
}
.lyt_radio {
  font-size: 14px;
  margin-right: 20px;
}
.lyt_radio input {
  position: relative;
  top: 2px;
  margin-right: 5px;
}



