/*  lyt_table(2017-07-07)
==================================================*/
.lyt_table table {
  width: 100%;
  table-layout: fixed;
  border: 1px solid #e5e5e5;
  background-color: #fff;
}

.lyt_table table thead tr {
  height: 60px;
  line-height: 60px;
  border-bottom: 1px solid #e5e5e5;
}

.lyt_table table thead tr th {
  font-weight: bold;
  text-align: center;
}

.lyt_table table tbody tr {
  border-top: 1px solid #e5e5e5;
}

.lyt_table table tbody tr td {
  padding: 20px 0;
  text-align: center;
  font-size: 14px;
}
.lyt_table table tbody tr td a{
  color: #6b2cc9;
}



