/*  lyt_uc(2017-07-07)
==================================================*/
.c_ucenter {
  width: 100%;
  overflow: hidden;
  background-color: #f8f9fd;
}

/* left */
.c_ucenter .uc_lt {
  width: 270px;
  height: 100%;
  float: left;
  filter: alpha(opacity=100 finishopacity=50 style=1 startx=0, starty=0, finishx=0, finishy=150) progid:DXImageTransform.Microsoft.gradient(startcolorstr=#3c20b4, endcolorstr=#792dd0, gradientType=0);
  -ms-filter: alpha(opacity=100 finishopacity=50 style=1 startx=0, starty=0, finishx=0, finishy=150) progid:DXImageTransform.Microsoft.gradient(startcolorstr=#3c20b4, endcolorstr=#792dd0, gradientType=0); /*IE8*/
  /*background: #682bc8; !* 一些不支持背景渐变的浏览器 *!*/
  background: -moz-linear-gradient(#3c20b4, #6b2cc9, #792dd0);
  background: -webkit-linear-gradient(#3c20b4, #6b2cc9, #792dd0);
  background: -o-linear-gradient(#3c20b4, #6b2cc9, #792dd0);
  background: linear-gradient(#3c20b4, #6b2cc9, #792dd0);
  transition: margin-left .3s ease 0s;
/*  overflow: hidden;
  margin-bottom: -3000px;
  padding-bottom: 3000px;*/
}

.c_ucenter .uc_lt.init {
  margin-left: -270px;
  transition: margin-left .3s ease 0s;
}

.c_ucenter .uc_logo img {
  margin: 30px auto;
}

.c_ucenter .uc_text {
  width: 100%;
  height: 50px;
  line-height: 50px;
  text-align: center;
  color: #fff;
  background-color: rgba(155, 143, 209, 0.5);
}

.c_ucenter .uc_text i {
  display: block;
  width: 25px;
  height: 25px;
  float: right;
  margin: 12px 20px 0;
  background: url("../../assets/icon_layout.png") no-repeat;
  background-position: -245px -32px;
}

.c_ucenter .uc_info {
  height: 100px;
  border-bottom: 1px solid #624ac6;
}

.c_ucenter .uc_info_lt {
  width: 45px;
  height: 45px;
  float: left;
  margin: 28px 10px 0 65px;
}

.c_ucenter .uc_info_rt {
  float: left;
  margin-top: 28px;
}

.c_ucenter .uc_info_rt h3 {
  width: 130px;
  color: #fff;
  font-size: 14px;
  margin-bottom: 5px;
  font-weight: normal;
  white-space: nowrap;
  text-overflow: ellipsis;
  overflow: hidden;
}

.c_ucenter .uc_info_rt i {
  display: inline-block;
  padding: 2px 5px;
  color: #fff;
  font-size: 12px;
  background-color: rgba(155, 143, 209, 0.5);
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
}
.c_ucenter .uc_nav {
  padding-bottom: 200px;
}
.c_ucenter .uc_nav ul {
  margin-top: 40px;
}

.c_ucenter .uc_nav ul li {
  height: 40px;
  line-height: 40px;
}
.c_ucenter #li4,
.c_ucenter #li6,
.c_ucenter #li10,
.c_ucenter #li13{
  margin-bottom: 35px;
}
.c_ucenter .uc_nav ul li i {
  display: block;
  width: 30px;
  height: 28px;
  float: left;
  margin: 6px 5px 0 70px;
  background: url("../../assets/icon_layout.png") no-repeat;
}

.c_ucenter .uc_nav i.icon0 {
  background-position: 5px 5px;
}

.c_ucenter .uc_nav i.icon1 {
  background-position: -30px 5px;
}

.c_ucenter .uc_nav i.icon2 {
  background-position: -65px 5px;
}

.c_ucenter .uc_nav i.icon3 {
  background-position: -100px 5px;
}

.c_ucenter .uc_nav i.icon4 {
  background-position: -135px 5px;
}

.c_ucenter .uc_nav i.icon5 {
  background-position: -170px 5px;
}

.c_ucenter .uc_nav i.icon6 {
  background-position: -205px 5px;
}

.c_ucenter .uc_nav i.icon7 {
  background-position: -240px 5px;
}

.c_ucenter .uc_nav i.icon8 {
  background-position: -275px 5px;
}

.c_ucenter .uc_nav i.icon9 {
  background-position: 7px -30px;
}

.c_ucenter .uc_nav i.icon10 {
  background-position: -27px -30px;
}

.c_ucenter .uc_nav i.icon11 {
  background-position: -62px -30px;
}

.c_ucenter .uc_nav i.icon12 {
  background-position: -170px -30px;
}

.c_ucenter .uc_nav i.icon13 {
  background-position: -205px -30px;
}
.c_ucenter .uc_nav i.icon14 {
  background-position: -274px -30px;
}
.c_ucenter .uc_nav ul li a {
  color: #ffffff;
}

.c_ucenter .uc_nav ul li:hover,
.c_ucenter .uc_nav ul li.active{
  color: #fff;
  background-color: rgba(155, 143, 209, 0.5);
}

/* right */
.c_ucenter .uc_rt {
  width: 100%;
  background-color: #f8f9fd;
}

.c_ucenter .uc_rt .uc_rt_top {
  height: 70px;
  border-bottom: 1px solid #e5e5e5;
  background-color: #fff;
}

.c_ucenter .btn_contr_sidebar {
  width: 45px;
  height: 30px;
  float: left;
  cursor: pointer;
  margin: 20px 0 0 40px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  border-radius: 3px;
  background: #6f0afe url("../../assets/icon_layout.png") no-repeat;
  background-position: -95px -30px;
}

.c_ucenter .btn_sign_out {
  float: right;
  height: 25px;
  line-height: 25px;
  margin: 22px 50px 0;
  color: #555;
  cursor: pointer;
}

.c_ucenter .btn_sign_out i {
  width: 30px;
  height: 25px;
  float: left;
  background: url("../../assets/icon_layout.png") no-repeat;
  background-position: -140px -30px;
}
.c_ucenter .uc_rt .uc_rt_main{
  padding: 0 45px 45px 310px;
}
.c_ucenter .uc_rt_main.init{
  padding: 45px;
}



